*{
  margin: 0;
  padding: 0;
}
.container1{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url('https://th.bing.com/th/id/OIP.hYr6wDEeHA4FMuTN-PFU5wHaEK?rs=1&pid=ImgDetMain')no-repeat center center/cover;
}
.box{
  width: 35%;
  background-color: rgba(235 , 235, 235,50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 30px;
}
.button{
  display: flex;
}
@media(max-width:1650px){
  .box{
    width: 45%;
  }
}
@media(max-width:1300px){
  .box{
    width: 55%;
  }
}
@media(max-width:1000px){
  .box{
    width: 65%;
  }
}
@media(max-width:800px){
  .box{
    width: 85%;
  }
}
@media(max-width:700px){
  .box{
    width: 90%;
  }
}
@media(max-width:600px){
  h1{
    font-size: 2rem !important;
  }
}